.dialogs {
    display: flex;
    flex-flow: row nowrap;
    height: 350px;
}

.dialogsItems {
    height: 315px;
    border-right: 2px solid lightgrey;
    width: 200px;
    padding-top: 20px;
    position: fixed;
    top: 50px;
}


.dialog a {
    text-decoration: none;
    color: #000 !important;
    font-size: 16px;
    padding: 12px 15px;
    display: block;
}

.active {
    background-color: #f0f0f0 !important;
}

.messages {
    margin-top: 60px;
    padding: 10px;
    margin-left: 200px;
}

.messages .message {
    margin: 10px;
}

.messages .buttonPart {
    margin-top: 20px;
}

.buttons {
    padding: 13px;
    width: 150px;
    text-align: center;
    font-size: 15px;
    cursor: pointer;
    outline: none;
    background-color: #5692d7;
    color: #fff;
    border-radius: 3px;
    border: none;
    transition: .3s all;
    margin-left: 10px;
}

.buttons:hover {
    background-color: #407bbd;
}

.buttons:active {
    background-color: lightgray;
    box-shadow: 0 1px #666;
    transform: translateY(4px);
}

.textModule {
    margin: 10px;
    display: flex;
    flex-flow: row nowrap;
}

.textarea {
    width: 300px !important;
    height: 50px !important;
    max-width: 600px !important;
    border: 1px solid #5692d7;
    font-family: sans-serif;
    max-height: 100px;
    padding: 15px;
    box-sizing: border-box;
    margin-right: 10px !important;
    border-radius: 3px;
}